.back_to_the_future_dark {
    position: fixed;
    top: calc(63vw);
    right: 0px;    
    left: calc(-80vw);
    pointer-events: none;
    width: 200vw;
    height: 200vh;
    mix-blend-mode: color;
    background: radial-gradient(50% 50% at 50% 50%, rgba(7, 252, 170, 0.063) 0%, rgba(255, 255, 255, 0) 100%);
    transform: translateY(-100vh);
    will-change: background;
    transition: background 450ms ease 0s;
}

.back_to_the_future_day {
    position: fixed;
    top: calc(63vw);
    right: 0px;    
    left: calc(-80vw);
    pointer-events: none;
    width: 200vw;
    height: 200vh;
    mix-blend-mode: color;
    background: radial-gradient(50% 50% at 50% 50%, rgb(2 150 13 / 18%) 0%, rgba(255, 255, 255, 0) 100%);
    transform: translateY(-100vh);
    will-change: background;
    transition: background 450ms ease 0s;
}
